import {
  Body,
  Controller,
  Delete,
  Get,
  Param,
  Post,
  Put,
} from '@nestjs/common';
import { VariantService } from './variant.service';
import { CreateVariantDto } from './dto/create-variant.dto';

@Controller('variant')
export class VariantController {
  constructor(private variantService: VariantService) {}

  @Get(':id')
  getVariants(@Param('id') id: number) {
    const productId = +id;
    console.log('product Id given for finding variants', productId);
    return this.variantService.getVariants(productId);
  }

  @Post(':id')
  creatVariant(@Param('id') id: string, @Body() dto: CreateVariantDto) {
    const productId = +id;
    return this.variantService.createVariant(productId, dto);
  }

  @Put(':id')
  updateVariant(@Param('id') id: string, @Body() dto: CreateVariantDto) {
    const variantId = +id;
    return this.variantService.updateVariant(variantId, dto);
  }

  @Delete(':id')
  deleteVariant(@Param('id') id: string) {
    const variantId = +id;
    return this.variantService.deleteVariant(variantId);
  }
}
