import { BadRequestException, Injectable } from '@nestjs/common';
import { CreateTaxDto } from './dto/create-tax.dto';
import { PrismaService } from '@hotel/api/data-access-db';

@Injectable()
export class TaxService {
  constructor(private prismaService: PrismaService) {}

  async getTaxes() {
    try {
      return this.prismaService.tax.findMany();
    } catch (error) {
      throw new BadRequestException({ error: error });
    }
  }
  async createTax(tax: CreateTaxDto) {
    try {
      return await this.prismaService.tax.create({
        data: {
          name: tax.name,
          printName: tax.printName,
          isPercentage: tax.isPercentage,
          value: +tax.value,
          companyId: 1,
        },
      });
    } catch (error) {
      throw new BadRequestException({ error: error });
    }
  }

  async updateTax(id: number, tax: CreateTaxDto) {
    try {
      return await this.prismaService.tax.update({
        where: { id },
        data: {
          name: tax.name,
          printName: tax.printName,
          isPercentage: tax.isPercentage,
          value: +tax.value,
          isActive: tax.isActive,
        },
      });
    } catch (error) {
      throw new BadRequestException({ error: error });
    }
  }
}
