import { Body, Controller, Get, Param, Patch, Post } from '@nestjs/common';
import { KotService } from './kot.services';

@Controller('kot')
export class KotController {
  constructor(private kotService: KotService) {}

  @Get()
  getOpenKots() {
    console.log('cotoller called?');
    return this.kotService.getOpenKots();
  }

  @Patch()
  completeKot(@Body() data: any) {
    const kotId = +data.kotId;
    return this.kotService.completeKot(kotId);
  }

  @Get(':id')
  getOrderKots(@Param() id: any) {
    console.log(id);
    return id;
  }
}
