import { Controller, Get, Param, Req, UseGuards } from '@nestjs/common';
import { HomeDeliveryService } from './home-delivery.service';
import { JwtAuthGuard } from '@hotel/api/feature-auth';

@Controller('delivery')
export class HomeDeliveryController {
  constructor(private homedeliveryService: HomeDeliveryService) {}

  @UseGuards(JwtAuthGuard)
  @Get('myorders')
  getCompletedHomeDeliveryOrders(@Req() req: any) {
    console.log('getting closed home deliveries');
    const user = req.user;
    return this.homedeliveryService.getClosedHomeDeliveryOrdersOfTheCurrentSession(
      user
    );
  }

  @UseGuards(JwtAuthGuard)
  @Get()
  getAssignedDeliveries(@Req() req: any) {
    const user = req.user;
    return this.homedeliveryService.getNonClosedHomeDeliveryOrdersOfTheCurrentSession(
      user
    );
  }

  @UseGuards(JwtAuthGuard)
  @Get('/:id')
  completeHomeDelivery(@Req() req: any, @Param() params: any) {
    const user = req.user;
    const orderId = +params.id;
    return this.homedeliveryService.deliverHomeDeliveryOrder(orderId);
  }
}
