import { PrismaService } from '@hotel/api/data-access-db';
import {
  OrderStatus,
  OrderType,
  SessionStatus,
  User,
} from '@hotel/common/types';
import {
  BadRequestException,
  Injectable,
  NotFoundException,
} from '@nestjs/common';

@Injectable()
export class HomeDeliveryService {
  constructor(private prismaService: PrismaService) {}

  async deliverHomeDeliveryOrder(orderId: number) {
    console.log('delivering home order.', orderId);
    try {
      await this.prismaService.order.update({
        where: { id: orderId },
        data: {
          orderStatus: OrderStatus.DELIVERED,
        },
      });
    } catch (error) {
      console.log(error);
      throw new Error();
    }
  }

  async getNonClosedHomeDeliveryOrdersOfTheCurrentSession(user: User) {
    // if cashier get all orders.
    // if deliver user get only assinged.
    // const isUserHasOnlyHomeDeliveryAccess =
    //   !user.isCashier && user.isHomeDeliveryUser;
    try {
      return await this.prismaService.order.findMany({
        where: {
          createdAt: { gte: await this.getActiveSessionStartTime() },
          orderType: OrderType.HOMEDELIVERY,
          orderStatus: {
            notIn: [OrderStatus.DELIVERED, OrderStatus.CANCELLED],
          },
          deliveryUserId: { not: null },
          ...(user.isHomeDeliveryUser && { deliveryUserId: user.id }),
        },
        include: { customer: true, user: true },
      });
    } catch (error) {
      throw new Error();
    }
  }

  async getClosedHomeDeliveryOrdersOfTheCurrentSession(user: User) {
    // if cashier get all orders.
    // if deliver user get only assinged.
    // const isUserHasOnlyHomeDeliveryAccess =
    //   !user.isCashier && user.isHomeDeliveryUser;
    try {
      return await this.prismaService.order.findMany({
        where: {
          createdAt: { gte: await this.getActiveSessionStartTime() },
          orderType: OrderType.HOMEDELIVERY,
          orderStatus: OrderStatus.DELIVERED,
          deliveryUserId: { not: null },
          ...(user.isHomeDeliveryUser && { deliveryUserId: user.id }),
        },
        include: { customer: true, user: true },
      });
    } catch (error) {
      throw new Error();
    }
  }

  async getHomeDeliveryOrdersOfTheCurrentSession(user: User) {
    // if cashier get all orders.
    // if deliver user get only assinged.
    // const isUserHasOnlyHomeDeliveryAccess =
    //   !user.isCashier && user.isHomeDeliveryUser;
    try {
      return await this.prismaService.order.findMany({
        where: {
          createdAt: { gte: await this.getActiveSessionStartTime() },
          orderType: OrderType.HOMEDELIVERY,
          orderStatus: { not: OrderStatus.CANCELLED },
          deliveryUserId: { not: null },
          ...(user.isHomeDeliveryUser && { deliveryUserId: user.id }),
        },
        include: { customer: true, user: true },
      });
    } catch (error) {
      throw new Error();
    }
  }

  async getTotalOrderCountAndTotalAmoutByDeliveryUser(user: User) {
    console.log('total count + sale');
  }

  private async getActiveSessionStartTime() {
    const activeSession = await this.prismaService.posSession.findFirst({
      where: {
        status: SessionStatus.ACTIVE,
      },
    });
    if (!activeSession) throw new NotFoundException('No active session');
    console.log('active session start time is', activeSession.startTime);
    return activeSession.startTime;
  }
}
