// import { CartItem } from './cart';
import { Modifier, Product } from './product';
import { Customer } from './table';
import { AppliedTaxInfo } from './taxes';
import { User } from './user';
export interface OrderItem {
  count: number;
  modifiers?: Modifier[];
  id?: number; //this is just for mapping the database id to orderitem
  status: OrderItemStatus;
  name?: string;
  kitchenUser?: User;
  key?: string;
  totalCountOfSameItem?: number;
  orderId?: string; //just to track what order it belongs.
  orderNumber?: number; // to track the order number
  orderItemType?: OrderItemType;
  amount?: number;
  customeKey?: string;
  product?: Product;
}

export interface OrderSummary {
  id: number;
  orderNumber?: number;
  createdAt?: Date;
  updatedAt?: Date;
  orderStatus: OrderStatus | string;
  paymentStatus?: PaymentStatus;
  orderItems?: OrderItem[];

  createdBy?: User;
  createdFor?: User;
  orderType?: string;
  customerName?: string | null;
  customer?: Customer;
  totalItemsCount?: number;
  totalQuantityCount?: number;
  appliedTaxes?: AppliedTaxInfo[];
  totalAmount?: number;
  taxedTotal?: number;
  customerId?: number;
  tableId?: number;
  createdUserId?: number;
  user?: User; // [NOTE] optional field to fetch created user.now schema is named user that is why the name user, change it to createdUser in prisma schema later on
  deliveryUserId?: number;
  kot?: any[];
}

// export interface AppliedTax {
//   name: string;
//   taxValue: number;
// }

export enum OrderType {
  TABLE = 'table',
  TAKEAWAY = 'takeaway',
  HOMEDELIVERY = 'homedelivery',
}

export enum OrderItemStatus {
  READY = 'ready',
  INPROGRESS = 'inprogress',
  WAITING = 'waiting',
}

export enum OrderStatus {
  PLACED = 'placed',
  INPROGRESS = 'inprogress',
  READY = 'ready',
  SERVED = 'served',
  DELIVERED = 'delivered',
  PAID = 'paid',
  CANCELLED = 'cancelled',
}

export enum OrderItemType {
  NEW = 'new',
  RUNNING = 'running',
}

export enum PaymentStatus {
  PAID = 'paid',
  NOTPAID = 'notpaid',
  CREDIT = 'credit',
}

export interface OrderStat {
  count: number;
  sum: number;
  paystat: PaymentStatus;
}

export interface StaffStat {
  name: string;
  id: number;
  orderCount: number;
}

export interface OrderItemEdit {
  orderItem: OrderItem;
  count: number;
}

export interface PaymentDetails {
  paidAmount: number;
  discount: number;
  balance: number;
  mode: string;
}
