import { PrismaService } from '@hotel/api/data-access-db';
import { Injectable } from '@nestjs/common';
import {
  UpdateCompanyDto,
  UpdateProdcutShortcutsDto,
} from './dto/company.update.dto';
import { getPrinters, print } from 'pdf-to-printer';
import { CompanyShortcutTypes } from '@hotel/common/types';

@Injectable()
export class CompanyService {
  constructor(private prismaService: PrismaService) {}

  async getCompanyDetails(id: number) {
    return await this.prismaService.company.findFirst({
      include: {
        taxes: true,
      },
    });
  }

  async updateCompany(updateCompanyDto: UpdateCompanyDto) {
    console.log(updateCompanyDto);
    return await this.prismaService.company.update({
      where: { id: updateCompanyDto.id },
      data: {
        ...updateCompanyDto,
        lastOrderNumber: +updateCompanyDto.lastOrderNumber,
        decimalZeros: +updateCompanyDto.decimalZeros,
      },
    });
  }

  async getPrinters() {
    try {
      const printers = await getPrinters();
      // console.log(printers);
      const mappedArr = printers.map((item) => ({
        name: item.name,
        printer: item.name,
      }));
      return mappedArr;
    } catch (error) {
      console.log('exception while getting priters list');
      console.log(error);
      throw new Error('Error on Getting installed printers');
    }
  }

  async updateProductShortcuts(
    updateProductShorcutdto: UpdateProdcutShortcutsDto,
    shortcutType: string
  ) {
    try {
      const company = await this.prismaService.company.update({
        where: { id: updateProductShorcutdto.id },
        data: {
          ...(shortcutType == CompanyShortcutTypes.PRODUCTSHORTCUTS && {
            productShortcuts: updateProductShorcutdto.shortcuts,
          }),
          ...(shortcutType == CompanyShortcutTypes.CUSTOMERSHORTCUTS && {
            customerShortcuts: updateProductShorcutdto.shortcuts,
          }),
        },
      });

      return company.productShortcuts;
    } catch (error) {
      console.log(error);
      throw new Error('failed to update products shortcuts');
    }
  }
}
