import { PrismaService } from '@hotel/api/data-access-db';
import { BadRequestException, Injectable } from '@nestjs/common';
import { CreateUserDto } from './dto/user-create.dto';

@Injectable()
export class UserService {
  constructor(private prismaService: PrismaService) {}
  async getUsers() {
    try {
      return await this.prismaService.user.findMany();
    } catch (error) {
      throw new BadRequestException({ error: error });
    }
  }

  async getDeliveryUsers() {
    try {
      return await this.prismaService.user.findMany({
        where: {
          isHomeDeliveryUser: true,
        },
      });
    } catch (error) {
      throw new BadRequestException({ error: error });
    }
  }

  //   [note] createuseris also part of auth module
  async createUser(user: CreateUserDto) {
    try {
      return await this.prismaService.user.create({
        data: {
          name: user.name,
          password: +user.password,
          username: user.username,
          isAdmin: false,
          isCashier: user.isCashier,
          isKitchenUser: user.isKitchenUser,
          isWaiter: user.isWaiter,
        },
      });
    } catch (error) {
      throw new BadRequestException({ error: error });
    }
  }
  async updateUser(id: number, user: CreateUserDto) {
    try {
      return await this.prismaService.user.update({
        where: { id },
        data: {
          name: user.name,
          password: +user.password,
          username: user.username,
          isAdmin: false,
          isCashier: user.isCashier,
          isKitchenUser: user.isKitchenUser,
          isWaiter: user.isWaiter,
          isHomeDeliveryUser: user.isHomeDeliveryUser,
        },
      });
    } catch (error) {
      throw new BadRequestException({ error: error });
    }
  }
}
