import {
  CartItem,
  OrderItemEdit,
  ResultantCartItem,
  User,
} from '@hotel/common/types';
import { IsDefined, IsNotEmpty, IsOptional } from 'class-validator';

export class CreateOrderDto {
  @IsDefined()
  @IsNotEmpty()
  cartItems!: { [key: string]: CartItem };
  orderItemsEdit!: { [key: string]: OrderItemEdit };
  resultantCartItems!: ResultantCartItem[];
  cartCreatedFor!: User;

  tableId!: number;
  customerId!: number;
  orderId!: number;

  total!: number;

  @IsOptional()
  note?: string;

  taxesApplied!: [];

  taxedTotal!: number;
  printKot!: boolean;
  deliveryUserId!: number;
}
