import { PrismaService } from '@hotel/api/data-access-db';
import { PaymentStatus, SessionStatus, User } from '@hotel/common/types';
import { Injectable, NotFoundException } from '@nestjs/common';

@Injectable()
export class OrderHistoryService {
  constructor(private prismaService: PrismaService) {}

  async getOrderHistoryOfTheCurrentSession(
    user: User,
    params: {
      skip?: number;
      take?: number;
    }
  ) {
    const activeSessionDate = await this.getActiveSessionStartTime();

    return await this.getOrderHisotryOfGivenPeriod(
      activeSessionDate,
      new Date(),
      user,
      params
    );
  }

  async getOrderHisotryOfGivenPeriod(
    startDateTime: Date,
    endDateTime: Date,
    user: User,
    params: {
      skip?: number;
      take?: number;
    }
  ) {
    // eslint-disable-next-line prefer-const
    let { skip, take } = params;

    if (!take) take = 10;
    console.log('values of skipt and take', `skip ${skip} - take ${take}`);
    try {
      if (!skip) {
        console.log(`in the skip 0 area skip values is ${skip}`);
        return this.prismaService.order.findMany({
          take,
          where: {
            // paymentStatus: { in: [PaymentStatus.CREDIT, PaymentStatus.PAID] },
            ...(!user.isCashier && { createdUserId: user.id }),
            // createdAt: { gte: startDateTime },
          },
          include: { customer: true, user: true },
          orderBy: { createdAt: 'desc' },
        });
      } else {
        return this.prismaService.order.findMany({
          skip,
          take,
          where: {
            paymentStatus: { in: [PaymentStatus.CREDIT, PaymentStatus.PAID] },
            ...(!user.isCashier && { createdUserId: user.id }),
          },
          orderBy: { createdAt: 'desc' },
          include: { customer: true, user: true },
        });
      }
    } catch (error) {
      throw new Error('Error on get order history');
    }
  }

  async getOrderHistoryOfGivenUserInTheCurrentSession(
    userId: number,
    params: {
      skip?: number;
      take?: number;
    }
  ) {
    const activeSessionDate = await this.getActiveSessionStartTime();
    return this.getOrderHistoryOfGivenPeriodOfAUser(
      activeSessionDate,
      new Date(),
      userId,
      params
    );
  }
  private async getOrderHistoryOfGivenPeriodOfAUser(
    startDateTime: Date,
    endDateTime: Date,
    userId: number,
    params: {
      skip?: number;
      take?: number;
    }
  ) {
    // eslint-disable-next-line prefer-const
    let { skip, take } = params;
    if (!take) take = 10;
    try {
      if (!skip) {
        console.log(`picking first set when skip is zero userid ${userId}`);

        const orders = await this.prismaService.order.findMany({
          take,
          where: {
            paymentStatus: { in: [PaymentStatus.CREDIT, PaymentStatus.PAID] },
            createdUserId: +userId,
            // createdAt: { gte: startDateTime },
          },
          include: { customer: true, user: true },
          orderBy: { createdAt: 'desc' },
        });
        // console.log('orders are', orders);
        return orders;
      } else {
        return await this.prismaService.order.findMany({
          skip,
          take,
          where: {
            // paymentStatus: { in: [PaymentStatus.CREDIT, PaymentStatus.PAID] },
            createdUserId: userId,
            // createdAt: { gte: startDateTime },
          },
          include: { customer: true, user: true },
          orderBy: { createdAt: 'desc' },
        });
      }
    } catch (error) {
      throw new Error('Error on get order history');
    }
  }

  async getOrderHistoryOfOrderNumberAsArr(orderNumber: number) {
    try {
      return this.prismaService.order.findMany({
        where: { orderNumber },
        include: { customer: true, user: true },
      });
    } catch (error) {
      throw new Error('Error on get order history');
    }
  }
  private async getActiveSessionStartTime() {
    const activeSession = await this.prismaService.posSession.findFirst({
      where: {
        status: SessionStatus.ACTIVE,
      },
    });
    if (!activeSession) throw new NotFoundException('No active session');
    console.log('active session start time is', activeSession.startTime);
    return activeSession.startTime;
  }
}
