import { PuppeteerService } from '@hotel/api/common';
import { PrismaService } from '@hotel/api/data-access-db';
import { StatsService } from '@hotel/api/feature-stat';
import { PaymentStatus } from '@hotel/common/types';
import {
  dateTimeToDateHHMM,
  getAppliedTaxesAndTaxesTotal,
  getOnlyCurrentDateWithoutTime,
} from '@hotel/common/util';
import {
  BadGatewayException,
  Injectable,
  NotFoundException,
} from '@nestjs/common';

@Injectable()
export class SessionReportService {
  constructor(
    private prismaService: PrismaService,
    private statService: StatsService,
    private pupeteerService: PuppeteerService
  ) {}
  async downloadSessionReport(sessionId: number) {
    try {
      // get the start and end date of the session Id.
      const session = await this.getSessionDetails(sessionId);

      // get company taxes.
      const company = await this.prismaService.company.findFirst({
        include: { taxes: true },
      });

      if (!company)
        throw new NotFoundException({}, 'Company Data Could not be read.');

      const taxes = company.taxes;
      console.log('creating pdf for the session status of ', sessionId);
      if (!session)
        throw new NotFoundException({}, 'No Session with the given ID');
      const startTime = session.startTime;
      const endTime = session.endTime!;
      const initialCash = session.initialCash;

      const reportSummary = await this.statService.getReportStatsForThePeriod(
        startTime,
        endTime
      );

      const prductSummary = await this.statService.getProductStatsForThePeriod(
        startTime,
        endTime
      );

      const staffCountSummary =
        await this.statService.getStaffCountStatsForThePeriod(
          startTime,
          endTime
        );

      const staffAmoutSummary =
        await this.statService.getStaffAmountStatsForThePeriod(
          startTime,
          endTime
        );

      // console.log('statff amount, ', staffAmoutSummary);

      const homeDeliveryCountSummary =
        await this.statService.getHomeDeliveryStatCountViceForThePeriod(
          startTime,
          endTime
        );

      // console.log('home delivery count', homeDeliveryCountSummary);

      const homeDeliveryAmoutSummary =
        await this.statService.getHomeDeliveryStatAmountViceForThePeriod(
          startTime,
          endTime
        );
      // console.log('homedevliery amount', homeDeliveryAmoutSummary);
      const formattedStartTime = dateTimeToDateHHMM(startTime);
      const formattedEndTime = dateTimeToDateHHMM(endTime);
      const currentDayOnlyDate = getOnlyCurrentDateWithoutTime();
      // console.log('product summary', prductSummary);
      const noOfDecimalPlaces = company?.decimalZeros
        ? company.decimalZeros
        : 3;
      const { ordersStatArr, totalOrderDetail, totalCashSale } =
        this.formatOrderStatV2(reportSummary, noOfDecimalPlaces);
      const { taxesApplied, taxedTotal } = getAppliedTaxesAndTaxesTotal(
        totalCashSale,
        taxes
      );
      const totalTaxCollected = taxedTotal - totalCashSale;

      const htmlInputData: any = {
        date: currentDayOnlyDate,
        startTime: formattedStartTime,
        endTime: formattedEndTime,
        initialCash: initialCash.toFixed(noOfDecimalPlaces),
        totalCashSale: totalCashSale.toFixed(noOfDecimalPlaces),
        totalCashInDrawer: (initialCash + taxedTotal).toFixed(
          noOfDecimalPlaces
        ),
        productStatArr: prductSummary,
        ordersStatArr,
        totalOrderDetail,
        totalTaxCollected: totalTaxCollected.toFixed(noOfDecimalPlaces),
        stafCountStatArr: staffCountSummary.length ? staffCountSummary : [],
        stafAmoutStatArr: staffAmoutSummary.length ? staffAmoutSummary : [],
        homeDeliveryCountStatArr: homeDeliveryCountSummary.length
          ? homeDeliveryCountSummary
          : [],
        homeDeliveryAmoutStatArr: homeDeliveryAmoutSummary.length
          ? homeDeliveryAmoutSummary
          : [],
      };
      const pdfStream: Buffer = await this.pupeteerService.getReportPdfStream(
        'sessionSummary',
        'views',
        'pdf',
        'sessionEndsummary.report.html',
        htmlInputData
      );
      return {
        pdfStream,
        reportName: `session report - ${startTime} -- ${endTime}`,
      };
    } catch (error) {
      console.log(error);
      throw new BadGatewayException({ error });
    }
  }

  async getSessionDetails(sessionId: number) {
    return await this.prismaService.posSession.findUnique({
      where: { id: sessionId },
    });
  }

  private formatOrderStat(orderStat: any[], noOfDecimalPlaces: number) {
    let totalOrders = 0;
    let totalOrderSum = 0;
    let totalCashSale = 0;
    const mappedOrders = orderStat.map((item) => {
      let description = '';
      switch (item.paystat) {
        case PaymentStatus.CREDIT:
          description = 'Credit Orders';
          break;
        case PaymentStatus.PAID:
          description = 'Paid Orders';
          totalCashSale = totalCashSale + item.sum;
          break;
        case PaymentStatus.NOTPAID:
          description = 'Non Settled Orders';
          break;
        default:
          console.log(item.paystat);
          break;
      }
      totalOrders = totalOrders + item.count;
      totalOrderSum = totalOrderSum + item.sum;
      return {
        description: description,
        count: item.count,
        sum: item.sum.toFixed(noOfDecimalPlaces),
      };
    });

    const totalOrderDetail = {
      description: 'Total Orders',
      count: totalOrders,
      totalOrderSum: totalOrderSum.toFixed(noOfDecimalPlaces),
    };

    return { ordersStatArr: mappedOrders, totalOrderDetail, totalCashSale };
  }

  private formatOrderStatV2(orderStat: any[], noOfDecimalPlaces: number) {
    let totalOrders = 0;
    let totalOrderSum = 0;
    let totalCashSale = 0;
    let totalDiscount = 0;
    const mappedOrders = orderStat.map((item) => {
      console.log('discount of the current', item.discount);
      totalDiscount = totalDiscount + Number.parseFloat(item.discount);
      totalOrderSum = totalOrderSum + item.sum;
      totalOrders = totalOrders + item.count;
      if (item.paystat == 'cash') {
        totalCashSale = totalCashSale + item.sum;
      }
      return {
        description: item.paystat,
        count: item.count,
        sum: item.sum.toFixed(noOfDecimalPlaces),
        discount: item.discount,
      };
    });
    console.log('total discount', totalDiscount);
    const totalOrderDetail = {
      description: 'Total Orders',
      count: totalOrders,
      totalOrderSum: totalOrderSum.toFixed(noOfDecimalPlaces),
      totalDiscount: totalDiscount.toFixed(noOfDecimalPlaces),
    };

    console.log('total order details', totalOrderDetail);
    console.log('mapped array', mappedOrders);
    return { ordersStatArr: mappedOrders, totalOrderDetail, totalCashSale };
  }
}
