import { Body, Controller, Get, Patch, Post, Query } from '@nestjs/common';
import { CompanyService } from './company.service';
import {
  UpdateCompanyDto,
  UpdateProdcutShortcutsDto,
} from './dto/company.update.dto';
import { CompanyShortcutTypes } from '@hotel/common/types';

@Controller('company')
export class ApiFeatureCompanyController {
  constructor(private companyService: CompanyService) {}
  @Get()
  getCompanyDetails() {
    console.log('jafar get copamy ');
    return this.companyService.getCompanyDetails(1);
  }

  @Post()
  updateCompany(@Body() companyUpdateDto: UpdateCompanyDto) {
    return this.companyService.updateCompany(companyUpdateDto);
  }

  @Get('printers')
  getConnectedPrinters() {
    return this.companyService.getPrinters();
  }

  @Patch()
  updateProductShortcuts(
    @Body() updateProductShortcuts: UpdateProdcutShortcutsDto,
    @Query() data: any
  ) {
    console.log('query data', data);
    const shortcutType = data.shortcutName;
    if (
      shortcutType == CompanyShortcutTypes.CUSTOMERSHORTCUTS ||
      shortcutType == CompanyShortcutTypes.PRODUCTSHORTCUTS
    ) {
      return this.companyService.updateProductShortcuts(
        updateProductShortcuts,
        shortcutType
      );
    }
    throw Error('Query data does not match shortcut name');
  }
}
