      //     data: {
      //       kitchenId: +kitId,
      //       updatedUserId: appUser.id!,

      //       orderItems: {
      //         create: cartItems.map((cartItem) => {
      //           const orderItem = {
      //             count: cartItem.count,
      //             customeKey: cartItem.key ?? '',
      //             name:
      //               cartItem.product.name +
      //               (cartItem.variant ? '-' + cartItem.variant.name : ''),
