import { PrismaService } from '@hotel/api/data-access-db';
import { BadRequestException, Injectable } from '@nestjs/common';
import { CreateVariantDto } from './dto/create-variant.dto';

@Injectable()
export class VariantService {
  constructor(private prismaService: PrismaService) {}

  async getVariants(productId: number) {
    return await this.prismaService.variant.findMany({
      where: {
        productId: productId,
      },
    });
  }

  async createVariant(productId: number, createVariantDto: CreateVariantDto) {
    try {
      const variant = await this.prismaService.variant.create({
        data: {
          name: createVariantDto.name,
          price: +createVariantDto.price,
          productId: productId,
        },
      });

      return variant;
    } catch (error) {
      console.log(error);
      throw new BadRequestException({ error: 'error happened' });
    }
  }

  async updateVariant(variantId: number, updateVariantDto: CreateVariantDto) {
    try {
      const variant = await this.prismaService.variant.update({
        where: { id: variantId },
        data: {
          name: updateVariantDto.name,
          price: +updateVariantDto.price,
        },
      });

      return variant;
    } catch (error) {
      console.log(error);
      throw new BadRequestException({ error: 'error happened' });
    }
  }

  async deleteVariant(variantId: number) {
    try {
      const variant = await this.prismaService.variant.delete({
        where: { id: variantId },
      });

      return variant;
    } catch (error) {
      console.log(error);
      throw new BadRequestException({ error: 'error happened' });
    }
  }
}
