import { PrismaService } from '@hotel/api/data-access-db';
import { OrderStatus, PaymentStatus } from '@hotel/common/types';
import { Injectable } from '@nestjs/common';
import { CreateFloorDto } from './dto/create-floor.dto';

@Injectable()
export class FloorService {
  constructor(private prismaService: PrismaService) {}

  async getFloors() {
    return await this.prismaService.floor.findMany();
  }

  async getFloorTables(floorId: number) {
    return await this.prismaService.floor.findFirst({
      where: { id: floorId },
      select: {
        id: true,
        tables: {
          include: {
            orders: {
              where: {
                AND: [
                  {
                    orderStatus: {
                      notIn: [OrderStatus.SERVED, OrderStatus.CANCELLED],
                    },
                  },
                  { paymentStatus: PaymentStatus.NOTPAID },
                ],
              },
              include: {
                customer: true,
              },
            },
          },
          orderBy: { name: 'asc' },
        },
      },
    });
  }

  async createFloor(floor: CreateFloorDto) {
    try {
      return await this.prismaService.floor.create({
        data: {
          name: floor.name,
        },
      });
    } catch (error) {
      throw new Error('unable to create floor');
    }
  }

  async editFloor(id: number, floor: CreateFloorDto) {
    try {
      return await this.prismaService.floor.update({
        where: { id },
        data: {
          name: floor.name,
        },
      });
    } catch (error) {
      throw new Error('floor update failed');
    }
  }
}
