import { JwtAuthGuard } from '@hotel/api/feature-auth';
import { Controller, Get, Param, Query, Req, UseGuards } from '@nestjs/common';
import { OrderHistoryService } from './order-history.service';

@Controller('ohistory')
export class OrderHistoryController {
  constructor(private orderHistoryService: OrderHistoryService) {}

  @UseGuards(JwtAuthGuard)
  @Get('ordernumber/:id')
  getOrderHistoryOfOrderAsArray(@Req() req: any, @Param() param: any) {
    const orderId = +param.id;
    console.log('searching by order number');
    return this.orderHistoryService.getOrderHistoryOfOrderNumberAsArr(orderId);
  }

  @UseGuards(JwtAuthGuard)
  @Get()
  getOrderHistoryOfCurrentSession(
    @Req() req: any,
    @Query('skip') skip: any,
    @Query('take') take: any
  ) {
    const user = req.user;
    console.log('inside get of history module');
    // return 'hello';
    return this.orderHistoryService.getOrderHistoryOfTheCurrentSession(user, {
      skip: +skip,
      take: +take,
    });
  }

  @UseGuards(JwtAuthGuard)
  @Get('user')
  getOrderHistoryOfCurrentSessionGivenUser(
    @Req() req: any,
    @Query('skip') skip: any,
    @Query('take') take: any,
    @Query('userId') userId: any
  ) {
    const user = req.user;
    console.log('skip take userId', `${skip} - ${take} = ${userId}`);
    return this.orderHistoryService.getOrderHistoryOfGivenUserInTheCurrentSession(
      +userId,
      {
        skip: +skip,
        take: +take,
      }
    );
  }
}
