import { Body, Controller, Get, Param, Post, Put } from '@nestjs/common';
import { FloorService } from './floor.service';
import { CreateFloorDto } from './dto/create-floor.dto';

@Controller('floor')
export class FloorController {
  constructor(private floorService: FloorService) {}

  @Get()
  getFloors() {
    return this.floorService.getFloors();
  }

  @Get(':id')
  getFloorTables(@Param() params: any) {
    const id = +params.id;
    return this.floorService.getFloorTables(id);
  }

  @Post()
  createProduct(@Body() params: CreateFloorDto) {
    return this.floorService.createFloor(params);
  }

  @Put(':id')
  updateProduct(@Body() body: CreateFloorDto, @Param('id') id: any) {
    const floorId = +id;
    console.log('floor id', floorId);
    return this.floorService.editFloor(floorId, body);
  }
}
